.nav {
  grid-area: n;
  background: linear-gradient(#013e92,#3d1984bf);
  padding: 1.5rem;
}
.item {
  padding-bottom: 1rem;
}
.active {
  color: #ffe100;
}
a {
  color: #fff;
}
a:hover {
  color: #ffe100;
  transition: all 0.3s;
}
.img {
  width: 75px;
  height: 75px;
  overflow: hidden;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  background-color: #eee;
}
.friends {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.friend {
  margin: 0 1rem;
  text-align: center;
  color: #fff;
}
.nav h4 {
  color: #0ff;
  text-align: center;
}