* {
  outline: none;
}
.app-wrapper {
  display: grid;
  grid-template-areas:
    "h h"
    "n c"
    "f f";
  grid-template-rows: 100px 1fr;
  grid-template-columns: 2fr 10fr;
  /*grid-gap: 10px;*/
  font-family: "Montserrat";
  color: #033f92;
}
.app-wrapper-content {
  grid-area: c;
  background-color: #3ad6ec1a;
  padding: 2rem;
}
img {
  width: 100%;
}
a {
  text-decoration: none;
}
.flex {
  display: flex;
  align-items: center;
}
textarea {
  padding: 1rem;
  border-radius: 0.5rem;
}
button {
  border: none;
  cursor: pointer;
  padding: 1rem 2rem;
  font-size: 1rem;
  border-radius: 0.5rem;
}
@media (max-width: 767px) {
  .app-wrapper {
    grid-template-areas:
      "h h"
      "n n"
      "c c"
      "f f";
  }
}