.img {
  display: flex;
  align-items: center;
  max-width: 100px;
  width: 100%;
  height: 100px;
  overflow: hidden;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  background-color: #eee;
  margin: 1rem;
}
.img img {
  width: 100%;
  height: auto;
}
.info div {
  padding: 0.5rem;
}
