.header {
  grid-area: h;
  background: linear-gradient(90deg, #38b9f900, #38b9f9 75%);
  padding: 0.5rem;
  position: relative;
}
.logo {
  position: relative;
  width: 85px;
  height: 85px;
  overflow: hidden;
}
.logo img {
  position: absolute;
}
.logo img:hover {
  -webkit-filter: invert(0.5);
  filter: invert(0.5);
}
.loginBlock {
  position: absolute;
  top: 0;
  right: 0;
  padding: 1rem 2rem;
}